/* CSS Document */
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, 
*:before, 
*:after {
  box-sizing: inherit;
}

img, 
embed, 
object, 
video {
  max-width: 100%;
}

img {
  width: 100%;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #252831;
  color: #333;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  font-size: 1rem;
  line-height: 2rem;
}

header{
  width: 100%;
}

a {
  color:  #ff4500;
  text-decoration: none;
}

a:hover, 
a:focus {
  color: #3397db;
  text-decoration: underline;
}

h1, 
h2, 
h3, 
h4, 
h5, 
h6 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0 0 2rem;
}

article, 
aside, 
canvas, 
details, 
figcaption, 
figure, 
footer, 
header, 
hgroup, 
menu, 
nav, 
section {
  display: block;
}

main {
  background-color: #f3f3f3;
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

footer {
  background-color: #444;
  color: #b1b1b1;
  font-size: 1.1rem;
  letter-spacing: 0.1rem;
  padding: 2.5rem;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}

footer a {
  color: #fff;
}

/* ====================== Navigation ====================== */
nav {
  width: 100%;
  height: 80px;
  background-color: #252831;
  text-align:center;
}

nav h1 {
  margin: auto;
  padding: 2rem 1rem;
}

nav h1 a {
  color: #fff;
  font-size: 2rem;
  font-weight: 200;
  letter-spacing: 1rem;
  text-transform: uppercase;
  min-height: 4.8rem;
  min-width: 4.8rem;
}

.map-container {
  width: 100%;
}

nav[aria-label="breadcrumb"] {
  margin: 0;
  height: auto;
}

.breadcrumb {
  padding: 1rem 4rem 1.6rem;
  list-style: none;
  background-color: #eee;
  font-size: 1.5rem;
  margin: 0;
  border-top: 1rem solid #007dbb;
  text-align: left;
}

/* Display list items side by side */
.breadcrumb li {
  display: inline;
}

/* Add a slash symbol (/) before/behind each list item */
.breadcrumb li+li:before {
  padding: 0.8rem;
  color: black;
  content: "/\00a0";
}

/* Add a color to all links inside the list */
.breadcrumb li a {
  color: #006cc9;
  text-decoration: none;
  min-height: 4.8rem;
  min-width: 4.8rem;
}

/* Add a color on mouse-over */
.breadcrumb li a:hover {
  color: #01447e;
  text-decoration: underline;
}

.container {
  width: 100%;
}

/* ====================== Map ====================== */
.map {
  height: 400px;
  width: 100%;
  background-color: #ccc;
}

/* ====================== Restaurant Filtering ====================== */
.filter-options {
  width: 100%;
  background-color: #007dbb;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.filter-options h3 {
  color: white;
  font-size: 2.2rem;
  font-weight: normal;
  line-height: 1;
  margin: 2rem 1rem 1rem 1rem;
  width: 100%;
}
.filter-options label {
  color: #fff;
}
.filter-options select {
  background-color: #fff;
  border: 1px solid #fff;
  font-family: Arial,sans-serif;
  font-size: 1.5rem;
  letter-spacing: 0;
  margin: 2rem 1rem;
  padding: 0 1rem;
  width: 100%;
  min-height: 4.8rem;
  min-width: 4.8rem;
}

.filter-options select:focus {
  outline: 0;
  box-shadow: 0 0 1px 2px  #aa5a00;
}

/* ====================== Restaurant Listing ====================== */
.restaurant-container {
  width: 100%;
}

.restaurants-list {
  background-color: #f3f3f3;
  list-style: outside none none;
  margin: 0;
  padding: 1rem;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.restaurant-container h3 {
  color: #252831;
  font-size: 3.2rem;
  font-weight: 300;
  letter-spacing: -0.1rem;
  padding-bottom: 0.2rem;
  text-align: center;
}

.restaurants-list li {
  background-color: #fff;
  border-radius: 1rem;
  border: 2px solid #ccc;
  font-family: Arial,sans-serif;
  margin: 1.5rem 0;
  min-height: 380px;
  padding: 0 1rem 1.5rem;
  text-align: center;
  width: 100%;
}

.restaurants-list .restaurant-img {
  background-color: #ccc;
  display: block;
  margin: 0;
  max-width: 100%;
}

.restaurants-list li h3 {
  color: #fff;
  background-color: #252831;
  padding: 1rem 0;
  font-family: Arial,sans-serif;
  font-size: 1.9rem;
  font-weight: 200;
  letter-spacing: 0;
  line-height: 1.3;
  border-radius: 0.5rem;
  margin: 2rem 0 1rem;
  text-transform: uppercase;
}

.restaurants-list p {
  margin: 0;
  font-size: 1.5rem;
}

.restaurants-list li a {
  background-color: #007dbb;
  border-bottom: .3rem solid #eee;
  color: #fff;
  display: inline-block;
  font-size: 1.3rem;
  margin: 1.5rem 0 0;
  padding: 0.8rem 3rem 1rem;
  border-radius: 0.5rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

.restaurants-list li a:hover,
.restaurants-list li a:focus {
  background-color: #aa5a00;
  text-decoration: underline;
  box-shadow: 0 0 1px 2px  #aa5a00;
  border-bottom: .3rem solid #252831;
}

/* ====================== Restaurant Details ====================== */
.inside header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 500;
}

.inside .map-container {
  background: blue none repeat scroll 0 0;
  height: 400px;
  width: 100%;
}

.inside .map {
  background-color: #ccc;
  height: 100%;
  width: 100%;
}

.inside .restaurant-container {
  padding: 1rem;
  width: 100%;
}

.restaurant-name {
  color: #252831;
  font-family: Arial,sans-serif;
  font-size: 2.6rem;
  font-weight: 200;
  letter-spacing: 0;
  margin: 1.5rem 0 3rem;
  text-transform: uppercase;
  line-height: 2.5rem;
  text-align: center;
}

.restaurant-img {
  width: 100%;
}

.restaurant-address {
  font-size: 1.6rem;
  margin: 1rem 0;
}

.restaurant-cuisine {
  background-color: #333;
  color: #ddd;
  font-size: 1.6rem;
  font-weight: 300;
  letter-spacing: 1rem;
  margin: 0 0 2rem;
  padding: 0.2rem 0;
  text-align: center;
  text-transform: uppercase;
  width: 100%;
}

.restaurant-container, 
.reviews-container {
  border-bottom: 0.1rem solid #d9d9d9;
  border-top: 0.1rem solid #fff;
  width: 100%;
}

.restaurant-container, 
.reviews-container {
  padding: 2rem 0 0 0;
}

.reviews-container h3 {
  color:  #252831;
  font-size: 3.2rem;
  font-weight: 300;
  letter-spacing: -0.1rem;
  padding-bottom: 0.2rem;
  text-align: center;
}

.reviews-list {
  margin: 0;
  padding: 1rem;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.reviews-list li {
  background-color: #fff;
  list-style-type: none;
  margin: 0 0 2rem;
  padding: 1rem;
  border-radius: 2rem 0;
  width: 100%;
}

.reviews-list li p {
  margin: 0 0 1rem;
  font-size: 1.5rem;
}

.reviews-list li p:first-child {
  font-size: 2.3rem;
  color: #fff;
  padding: 1rem 0 1rem 1rem;
  width: 45%;
  background-color: #252831;
  border-radius: 0.5rem 0 0 0.5rem;
  float: left;
}

.reviews-list li p:nth-child(2) {
  background-color: #252831;
  color: #aaa;
  padding: 1rem 1rem 1rem 0;
  width: 55%;
  border-radius: 0 0.5rem 0.5rem 0;
  text-align: right;
  float: right;
}

.reviews-list li p:nth-child(3) {
  background-color: #007dbb;
  color: #fff;
  font-weight: bold;
  padding: 0.8rem;
  border-radius: 0.5rem;
  display: inline-block;
  clear: both;
}


.restaurant-hours td {
  color: #666;
  font-size: 1.5rem;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background: #a56900;
  color: #fff;
  padding: 0.8rem 3rem 1rem;
  z-index: 100;
  border-bottom-right-radius: 0.8rem;
  font-size: 1.6rem;
}

.skip-link:focus {
  top: 0;
  color: #fff;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.sw-button {
  background-color: #007dbb;
  color: #fff;
  padding: 0.8rem 3rem 1rem;
  z-index: 100;
  border-bottom-right-radius: 0.8rem;
  font-size: 1.6rem;
  position: fixed;
  bottom: 0;
  right: 0;
}
